:root {
  --Lime: #d7da2f;
  --Red: #d73328;
  --White: #fff;
  --Slate-100: #e3f3fd;
  --Slate-300: #9abed5;
  --Slate-500: #6b94a8;
  --Slate-700: #4e6e7e;
  --Slate-900: #123040;
}

*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  background: var(--Slate-100);
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  font-family: Plus Jakarta Sans, sans-serif;
  font-size: 1.6rem;
  font-weight: 500;
  display: flex;
}

.mortgage-container {
  background: var(--White);
  color: var(--Slate-700);
  border-radius: 15px;
  grid-template-columns: repeat(2, 1fr);
  max-width: 100rem;
  display: grid;
}

.term-rate {
  gap: 2rem;
  display: flex;
}

.form-container {
  padding: 3.5rem;
}

.form-header {
  justify-content: space-between;
  margin-bottom: 2.5rem;
  display: flex;
}

a:link, a:visited {
  color: var(--Slate-700);
}

.tag {
  background-color: var(--Slate-100);
  cursor: pointer;
  padding: .7rem 1.5rem;
}

.title {
  color: var(--White);
  font-size: 2.2rem;
}

.form-title {
  color: var(--Slate-900);
}

.results-title {
  margin-bottom: 1.5rem;
  font-size: 2.2rem;
}

.input-container {
  border: 1px solid var(--Slate-700);
  transition: all .4;
  border-radius: 5px;
  gap: 0;
  width: fit-content;
  margin-top: 1rem;
  margin-bottom: 2rem;
  display: flex;
  overflow: hidden;
}

.input-container:focus-within .tag {
  background: var(--Lime);
}

.input-container:focus-within, .input-container:hover {
  border-color: var(--Lime);
}

.input-container:hover {
  cursor: pointer;
}

.input-container--1 {
  width: 100%;
}

input {
  cursor: pointer;
  color: var(--Slate-900);
  transition: all .4;
  border: none;
  outline: none;
  width: 100%;
  padding: .75rem 1rem;
  font-family: inherit;
  font-size: 1.6rem;
  font-weight: 700;
}

input:active, input:focus {
  cursor: auto;
}

.body-text, .label {
  color: var(--Slate-700);
}

.mortgage-type {
  width: 1rem;
  padding: .5rem;
}

.mortgage-type-label {
  color: var(--Slate-900);
  cursor: pointer;
  font-weight: 700;
}

.mortgage-type {
  margin-right: 1.5rem;
}

.mortgage-type:active {
  color: #e7e982;
}

.radio-container {
  width: 100%;
  padding: 1rem 2rem;
}

.radio-container:focus-within {
  background: #e7e982;
}

.results-container {
  background: var(--Slate-900);
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
  border-bottom-left-radius: 70px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 3.5rem;
  display: flex;
}

.results-container--1 {
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  display: flex;
}

.results-description {
  text-align: center;
  color: var(--Slate-300);
}

button {
  color: var(--Slate-900);
  background: var(--Lime);
  cursor: pointer;
  border: none;
  border-radius: 30rem;
  outline: none;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  margin-top: 3rem;
  padding: 1rem 3rem;
  font-family: inherit;
  font-size: 1.6rem;
  font-weight: 700;
  display: flex;
}

button:hover {
  background: #e7e982;
}

.body-text--2 {
  color: var(--Slate-300);
}

.card {
  border-top: .5rem solid var(--Lime);
  background: #0b1c26;
  border-radius: 4px;
  width: 100%;
  margin-top: 3.5rem;
  padding: 2rem;
}

.monthly-repayment, .term-repayment {
  margin-bottom: 1rem;
}

.repayment-result {
  border-bottom: 1px solid var(--Slate-700);
  color: var(--Lime);
  margin-bottom: 2.5rem;
  padding-bottom: 3rem;
  font-size: 4rem;
}

.total-result {
  color: var(--White);
  font-size: 2.2rem;
}
